
import Vue, { PropType } from "vue"

export default Vue.extend({
  props: {
    title: {
      type: String,
      required: false,
      default: null,
    },
    description: {
      type: String,
      required: false,
      default: null,
    },
    icon: {
      type: String,
      required: false,
      default: null,
    },
    href: {
      type: [] as PropType<any>,
      required: false,
      default: null,
    },
    tight: {
      type: Boolean,
      required: false,
      default: false,
    },
    elevated: {
      type: Boolean,
      required: false,
      default: false,
    },
    cursor: {
      type: Boolean,
      required: false,
      default: true,
    },
    truncate: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  data() {
    return {
      classes: "rounded-md overflow-x-hidden transition-colors",
    }
  },
})
